import {useState, useEffect} from 'react';
import config from 'src/commons/config-hoc';
import {Button} from 'antd';
// import {useDebounceEffect} from 'ahooks';

import {PageContent} from '@ra-lib/components';

function useDebounceEffect(effect, deps, delay = 0) {
    useEffect(() => {
        let result;
        const handler = setTimeout(() => {
            result = effect();
        }, delay);

        return () => {
            clearTimeout(handler);
            result && result();
        };

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, deps);
}


export default config({
    path: '/test',
})(function Test(props) {
    const [page, setPage] = useState(0);
    const [category, setCategory] = useState(0);

    useDebounceEffect(() => {
        console.log('category, page effect');
    }, [category, page]);

    useEffect(() => {
        console.log('category effect');
        setPage(0);
    }, [category]);

    return (
        <PageContent>
            <div>
                page: {page}
                <Button onClick={() => setPage(Date.now())}>改变page</Button>
            </div>
            <div>
                category: {category}
                <Button onClick={() => setCategory(Date.now())}>改变category</Button>
            </div>
        </PageContent>
    );

});
